<template>
  <div>
    <mt-cell>
      <div class="p1">联系人</div>
      <input type="text" class="p2" placeholder="请输入（选填）" v-model="form.contactName">
    </mt-cell>
    <mt-cell>
        <div class="p1">联系方式</div>
        <input type="text" class="p2" placeholder="请输入（选填）" v-model="form.contactTel" onkeyup="value=value.replace(/[^\d]/g,'') " ng-pattern="/[^a-zA-Z]/">
    </mt-cell>
    <div class="btn-div">
        <button type="primary" @click="modifyData" class="created">修改</button>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
// import Vue from 'vue'
  import axios from 'axios'
  import { Indicator } from 'mint-ui'
  import { MessageBox } from 'mint-ui'
  export default {
  data () {
    return {
      submitFormUrl:"/building-record/update-contact-info",
      form:{
        contactName:"",
        contactTel:"",
      },
      detailUrl:'/building-record/view',
      detailData:"",//根据记录获取详情数据
      recordId:"",//详情记录ID
    }
  },
  computed: {
  },
  watch: { // 监视双向绑定的数据数组
    
  },
  mounted() {
    this.init();
  },
  destroyed() {
  },
  methods: {
    init(){
      this.recordId=this.$route.query.buildId;
    },
    //修改数据
    modifyData(){
      let params={
        contactName:this.form.contactName,
        contactTel:this.form.contactTel,
        id:this.recordId
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
        .then((response)=> {
          let data = response.data;
          console.log("response",response);
          if(data.error_code==0){
             this.alertMsg('修改成功','success');
          }else{
             this.alertMsg('修改失败','error');
          }
        })
        .catch((error)=> {
          this.alertMsg(error);
        });
    },
    alertMsg(msg,type)
    {
      let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },

  },
  components:{
     
  }
}
</script>
<style>
  body{
    background-color: #f9f9f9;
    font-size: .28rem;
  }
</style>
<style scoped>
 body{
    font-family: "微软雅黑";
    background: #f9f9f9;
  }
  a{
    text-decoration: none;
  }
  .mint-cell{
    background: #f9f9f9;
  }
  .mint-cell .p1{
/*    height: 0.6rem;
    line-height: 0.1rem;*/
    font-size: .28rem;
    color: #333;
    display: inline-block;
    position: absolute;
    left: .5rem;
    /*top: 0.25rem;*/
  }
  .mint-cell .p2{
    font-size: .28rem;
    color: #333;
    display: inline-block;
    position: absolute;
    right: .5rem;
    border: 0;
    background: #f9f9f9;
    outline: none;
  }
.created{
    display: block;
    width: 2.5rem;
    background-color: #fff;
    height: 1rem;
    border-radius: 0.2rem;
    outline: none;
    border: 0;
    font-size:.28rem;
    margin:.5rem auto;
  }


</style>


