import Vue from 'vue'
import Router from 'vue-router'
import NewAndEditBuilding from '@/components/page/NewAndEditBuilding.vue'
import BuildingRecord from '@/components/page/BuildingRecord.vue'
import BuildingContact from '@/components/page/BuildingContact.vue'

Vue.use(Router)
/* eslint-disable */
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/building'
    },
    {
      path: '/building',
      name: 'NewAndEditBuilding',
      component: NewAndEditBuilding
    },
    {
      path: '/record',
      name: 'BuildingRecord',
      component: BuildingRecord
    },
    {
      path: '/contact',
      name: 'BuildingContact',
      component: BuildingContact
    }
  ]
})



