  methods: {
    init() {
      let type = this.getQueryString("type");
      console.log("type...",type);
      if (type=="record"&&!this.$route.query.id){
        this.$router.push('/record');
        return false;
      }
      this.getInitData();
    },
    getInitData() {
      // let data = this.$route.query.data;//编辑详情后台传来的数据
      let logoId = this.$route.query.id;//用来区分编辑和详情的id
      this.recordId = this.$route.query.recordId;//编辑详情过来的记录id
      // console.log("获取数据",data);
      if (logoId == 1){
        console.log("详情");
        this.showBtns = false;
        this.viewDisabled = true;
        this.getDataByBuildingId(1);
      } else if (logoId == 2){
        console.log("编辑");
        this.getDataByBuildingId(2);
      } else {
        console.log("新建");
        axios.get('/building-record/create').then((res)=>{
          console.log("/building-record/create..",res);
          let initData = res.data;
          if (initData.error_code!==undefined && initData.error_code !== 0){
            // MessageBox.alert('',{
            //   title:'',
            //   message: initData.msg,
            //   confirmButtonText:'知道了'
            // });
            this.showMain = false;
            this.errorMsg = initData.msg;
          } else {
            this.creatorID = initData.data.user_id;//创建人
            this.orgID = initData.data.org_id;//所在公司
            for(let key in initData.data.buildTypeList) {
              this.form.buildTypeList.push({key:key,label:initData.data.buildTypeList[key]});
            }
            console.log("initData.data.buildTypeList..",initData.data.buildTypeList);
            this.buildType = this.form.buildTypeList[this.form.buildTypeList.findIndex(item=>item.label=="写字楼")].key;
          }
        })
        .catch((error)=>{
          console.log("error..",error);
        });
      }
    },
    //根据点位ID获取点位信息
    getDataByBuildingId(singId){
      axios.get('/building-record/update?id='+this.recordId).then((res)=>{
          let initData = res;
          if (initData.status== 200){
            this.render(initData.data);
          } else {
            this.alertMsg(initData.statusText);
          }
        }).catch((error)=>{
             console.log("error..",error);
        });
