    // 微信授权获取经纬度
    getUserLocation(sucCallback){
      if (rootWxLocationFlag){
        wx.ready(()=>{
          wx.getLocation({
             type: 'gcj02',
             success: res=> {
                sucCallback(res.latitude,res.longitude);
             },
             cancel: res=> {
                console.log("cancel..",res);
                sucCallback(testLatitude,testLongitude);
             }
          });
        });
      } else {
        sucCallback(testLatitude,testLongitude);
      }
    },
    getLocation() {
      this.getUserLocation((latitude,longitude)=>{
        this.showMapContainer(latitude,longitude);
        this.getPois(latitude,longitude);
      });
    },
    getPois(latitude,longitude) {
      const location = latitude+","+longitude;
      console.log("location..",location);
      let data = {
        location: location,
        key: qqMapKey,
        get_poi: 1,
        poi_options: 'radius=2000;page_size=10;page_index=1;policy=2'
      };
      console.log("data..",data)
      data.output="jsonp";
      $.ajax({
          type:"get",
          dataType:'jsonp',
          data:data,
          jsonp:"callback",
          jsonpCallback:"QQmap",
          url:qqMapUrl,
          success: mapData=> {
            console.log("mapData..",mapData);
            qqMapCurrentCity = mapData.result.address_component.city;
            this.getNearbyList(mapData.result.pois);
          },
          error : err=> {
            this.alertMsg("腾讯地图服务器错误");
          }
      });
    },
    getNearbyList(pois) {
      console.log("pois..",pois);
      // 获取附近位置
      this.showItemPosition = true;
      this.nearbyAddress = pois.map(item=>{
        return {
          city: item.ad_info.city,
          district: item.ad_info.district,
          province: item.ad_info.province,
          title: item.title,
          address: item.address,
          location: item.location,
        }
      });
    },
    chooseNearbyPosition(item) {
      console.log(item)
      this.form.buildingName = item.title;
      this.form.address = item.address;
      this.form.city = item.city;
      this.form.area = item.district;
      this.form.province = item.province;
      this.form.buildLongitude = item.location.lng;
      this.form.buildLatitude = item.location.lat;
      this.mapShow = false;
      this.showMain = true;
    },
    showMapContainer(latitude,longitude) {
      // this.geocoderLocation();
      // console.log("nearbyAddress",this.nearbyAddress);
      $("#allmap").css("height",(window.innerHeight*(7.5/window.innerWidth)-4.2)+"rem");
      let geocoder,map,marker = null;
      let newCenter = new qq.maps.LatLng(latitude, longitude);
      map = new qq.maps.Map(document.getElementById('allmap'),{
          center: newCenter,
          zoom: 15,
          disableDefaultUI: true,
      });
      marker = new qq.maps.Marker({
          position: newCenter,
          draggable: false,
          map: map
      });
      qq.maps.event.addListener(map, 'center_changed', ()=> {
          marker.setPosition(map.getCenter())
          this.getPois(map.getCenter().lat,map.getCenter().lng)
      });
    },
    goBackMain() {
      this.mapShow = false;
      this.showMain = true;
      // console.log("1111",this.form.city,this.form.buildLongitude,this.form.buildLatitude);
    },
    showMap() {
      if (this.viewDisabled) {
        return false;
      }
      console.log("调地图");
      this.mapShow = true;
      this.showMain = false;
      if (!this.mapInit){
        this.mapInit = true;
        this.getLocation();
      }
      // console.log("nearbyAddress",this.nearbyAddress);
    },
    goBack() {
      console.log("点返回了");
      // history.pushState(null, null, document.URL);
      this.mapShow = false;
      this.showMain = true;
      // console.log("1111",this.form.city,this.form.buildLongitude,this.form.buildLatitude);
      // this.nearbyPosition = addressComponents.nearPois;
    },
    clickUploadImg(i) {
      if (this.viewDisabled) {
        return false;
      }
      console.log(this.buildingImgUrlData,this.hallImgUrlData);
      //开始
      let _this = this;
      wx.ready(function () {
        wx.checkJsApi({
          jsApiList: ['chooseImage'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
          success: function(res) {
            console.log('res',res);
          }
        });
        let localIdsArr;
        let serverId = [];
        let localIds;
        chooseImage(i);
        console.log(333333,_this);
        function chooseImage(i) {
          wx.chooseImage({
            count: 1, // 默认9
            sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ['camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: function (res) {
              console.log('chooseImage res',res);
              localIds = res.localIds;
              let imgLocalId = localIds[0];
              wx.uploadImage({
                localId: localIds.toString(),
                isShowProgressTip: 1,
                success: function(res) {
                  let mediaid = res.serverId;
                  console.log('mediaid',mediaid);
                  console.log("url",res);
                  if (i == 1){
                    console.log(1);
                    _this.buildingImgUrlData[0] = mediaid;
                    _this.imgInitUrl1 = imgLocalId;
                  } else if (i == 2){
                    console.log(2);
                    _this.buildingImgUrlData[1] = mediaid;
                    _this.imgInitUrl2 = imgLocalId;
                  } else if (i == 3){
                    _this.hallImgUrlData[0] = mediaid;
                    _this.imgInitUrl3 = imgLocalId;
                  } else {
                    _this.hallImgUrlData[1] = mediaid;
                    _this.imgInitUrl4 = imgLocalId;
                  }
                },
                error: function(data){
                  console.log(data,'error');
                }
              })
            },
            error: function(data){
              console.log(data,'error');
            }
          });
        }
      });
    },
    saveData() {
      if (this.form.buildingName == ""){
        this.alertMsg('楼宇名称不能为空');
        return false;
      }
      this.submitAction("1");
    },
    createdData() {
      if (this.form.buildingName == ""){
        this.alertMsg('楼宇名称不能为空');
        return false;
      }
      //验证分组(根据渠道类型进行分组，根据当前选择的渠道，进行验证对应字段)
      let subminBuildType = this.form.buildTypeList[this.form.buildTypeList.findIndex(item=>item.key==this.buildType)].label;
      if (this.$verify.check("public")){
        if (!this.checkPhone(this.form.contactTel)){
          this.alertMsg('请填写正确的电话号码');
          return;
        } else if (this.imgInitUrl1==addImgUrl||this.imgInitUrl2==addImgUrl) {
          this.alertMsg('请上传楼宇外观照片');
          return false;
        } else if (this.imgInitUrl3==addImgUrl||this.imgInitUrl4==addImgUrl) {
          this.alertMsg('请上传大厅全景照片');
          return false;
        } else if (subminBuildType=="写字楼"){
          if (this.$verify.check("build")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        } else if (subminBuildType=="园区"){
          if (this.$verify.check("park")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        } else if (subminBuildType=="学校"){
          if (this.$verify.check("school")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        } else if (subminBuildType=="医院"){
          if (this.$verify.check("hospital")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        } else if (subminBuildType=="公司"){
          if (this.$verify.check("company")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        } else {
          if (this.$verify.check("others")){
              this.submitAction("2");
          } else {
            this.alertMsg('请完善特殊信息');
          }
        }
      } else {
        this.alertMsg('请完善基础信息');
      }
    },
    submitAction(type){
      let specialInfo = {};
      let subminBuildType = this.form.buildTypeList[this.form.buildTypeList.findIndex(item=>item.key==this.buildType)].label;
      if (subminBuildType == '写字楼'){
        specialInfo = {//特殊信息json
          officeRent: this.build.checkOfficeRent,
          officeProperty: this.build.checkOfficeProperty,
          hallArea: this.build.checkHallArea,
          lobbyHigh: this.build.checkLobbyHigh,
          facadeMaterial: this.build.checkFacadeMaterial,
          groundFloor: this.build.checkGroundFloor,
          airConditioner: this.build.checkAirConditioner,
          elevatorsNumber: this.build.checkElevatorsNumber,
          companySize: this.build.checkCompanySize,
          companyNature: this.build.checkCompanyNature,
          yesOrNotOverThree: this.build.checkYesOrNotOverThree
        }
      } else if (subminBuildType == '园区'){
        specialInfo = {//特殊信息json
          officeRent: this.park.checkOfficeRent,
          officeProperty: this.park.checkOfficeProperty,
          hallArea: this.park.checkHallArea,
          lobbyHigh: this.park.checkLobbyHigh,
          facadeMaterial: this.park.checkFacadeMaterial,
          groundFloor: this.park.checkGroundFloor,
          airConditioner: this.park.checkAirConditioner,
          elevatorsNumber: this.park.checkElevatorsNumber,
          companySize: this.park.checkCompanySize,
          companyNature: this.park.checkCompanyNature,
          yesOrNotOverThree: this.park.checkYesOrNotOverThree
        }
      } else if (subminBuildType == '学校'){
        specialInfo = {//特殊信息json
          schoolTyle: this.school.checkSchoolTyle,
          numberOfSchool: this.school.checkNumberOfSchool,
          schoolPutinNumber: this.school.checkSchoolPutinNumber,
          chargingStandard: this.school.checkChargingStandard,
          sexRatio: this.school.checkSexRatio,
          livingExpenses: this.school.checkLivingExpenses,
          intramuralCommerce: this.school.checkIntramuralCommerce,
          lessThanThreeThousand: this.school.checkLessThanThreeThousand,
          buildingAttribute: this.school.checkBuildingAttribute,
          buildingArea: this.school.checkBuildingArea,
          buildingHeight: this.school.checkBuildingHeight
        }
      } else if (subminBuildType == '公司'){
        specialInfo = {//特殊信息json
          officeProperty: this.company.checkOfficeProperty,
          hallArea: this.company.checkHallArea,
          lobbyHigh: this.company.checkLobbyHigh,
          facadeMaterial: this.company.checkFacadeMaterial,
          groundFloor: this.company.checkGroundFloor,
          airConditioner: this.company.checkAirConditioner,
          elevatorsNumber: this.company.checkElevatorsNumber,
          sameLayer: this.company.checkSameLayer,
          numberOfCompanies: this.company.checkNumberOfCompanies,
          companyNature: this.company.checkCompanyNature,
          officeRent: this.company.checkOfficeRent,
          overTime: this.company.checkOverTime,
          onDutyMajority: this.company.checkOnDutyMajority,
          servingCoffee: this.company.checkServingCoffee,
          servingAfternoonTea: this.company.checkServingAfternoonTea,
          selfServiceEquipment: this.company.checkSelfServiceEquipment
        }
      } else if (subminBuildType == '医院'){
        specialInfo = {//特殊信息json
          hospitalType: this.hospital.checkHospitalType,
          healthWorkersNumber: this.hospital.checkHealthWorkersNumber,
          receptionQuantity: this.hospital.checkReceptionQuantity,
          hospitalBusiness: this.hospital.checkHospitalBusiness,
          buildingAttribute: this.hospital.checkBuildingAttribute,
          buildingHeight: this.hospital.checkBuildingHeight
        }
      } else {
        specialInfo = {
          checkFloorArea: this.others.checkFloorArea,
          checkClothing: this.others.checkClothing,
          checkCoverPopulation: this.others.checkCoverPopulation,
          checkFloorHeight: this.others.checkFloorHeight,
          checkFiftyCoffee: this.others.checkFiftyCoffee,
        };
      }
      let publicInfo={//公共信息json
        floor: this.form.floor,
        bCircle: this.form.bCircle,
        humanTraffic: this.form.checkHumanTraffic,
        scale: this.form.checkScale,
        roundBusiness: this.form.checkRoundBusiness,
        isMoreEntrance: this.form.checkIsMoreEntrance,
        equipmentLocation: this.form.checkEquipmentLocation,
        businessCircle: this.form.checkBusinessCircle,
        populationAge: this.form.checkPopulationAge,
        coffeeshop: this.form.checkCoffeeshop,
        hasOtherEquipment: this.form.checkHasOtherEquipment
      }
      let params={
        id: this.recordId,
        creatorID: this.creatorID,
        orgID: this.orgID,
        buildingName: this.form.buildingName,
        buildingStatus: type,//是创建还是保存草稿
        buildTypeID: this.buildType,//渠道类型
        city: this.form.city,
        area: this.form.area,
        province: this.province,
        address: this.form.address,
        buildLongitude: this.form.buildLongitude,
        buildLatitude: this.form.buildLatitude,
        contactName: this.form.contactName,
        contactTel: this.form.contactTel,
        buildPublicInfo: publicInfo,
        buildSpecialInfo: specialInfo,//特殊信息json
        buildAppearPic: this.buildingImgUrlData,//pc端不传
        buildHallPic: this.hallImgUrlData//pc端不传
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
      .then((response)=> {
        let data = response.data;
        if (data.error_code==0){
          if (type=="1"){
             this.alertMsg('保存草稿成功','success');
          } else {
            this.alertMsg('创建成功','success');
          }
          this.$router.push('/record');
        } else {
           this.alertMsg(data.msg);
        }
      })
      .catch((error)=> {
        this.alertMsg('接口错误');
      });
    },
    alertMsg(msg,type)
    {
      let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },
    showDifferenceType(type) {
      if (type == '写字楼'){
        this.showOfficeBuilding = true;
        this.showPark = false;
        this.showSchool = false;
        this.showCompany = false;
        this.showHospital = false;
        this.showOthers = false;
      } else if (type == '园区'){
        console.log("渠道类型");
        this.showOfficeBuilding = false;
        this.showPark = true;
        this.showSchool = false;
        this.showCompany = false;
        this.showHospital = false;
        this.showOthers = false;
      } else if (type == '学校'){
        this.showOfficeBuilding = false;
        this.showPark = false;
        this.showSchool = true;
        this.showCompany = false;
        this.showHospital = false;
        this.showOthers = false;
      } else if (type == '公司'){
        this.showOfficeBuilding = false;
        this.showPark = false;
        this.showSchool = false;
        this.showCompany = true;
        this.showHospital = false;
        this.showOthers = false;
      } else if (type == '医院'){
        this.showOfficeBuilding = false;
        this.showPark = false;
        this.showSchool = false;
        this.showCompany = false;
        this.showHospital = true;
        this.showOthers = false;
      } else {
        console.log("else",type);
        this.showOfficeBuilding = false;
        this.showPark = false;
        this.showSchool = false;
        this.showCompany = false;
        this.showHospital = false;
        this.showOthers = true;
      }
    },
    obtainOptionIndex(event) {
      // console.log('444',event.target.value);
      let targetValue = event.target.value;
      this.findOptionLabel(targetValue);
    },
    findOptionLabel(value) {
      let currentLabel = this.form.buildTypeList[this.form.buildTypeList.findIndex(item=>item.key==value)].label;
      this.showDifferenceType(currentLabel);
    },
    checkPhone(value) {
      if (value==="") {
        return true;
      }else{
        var myReg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;//手机号验证
        var telReg=/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$///座机号验证
        if (!myReg.test(value)&&!telReg.test(value)) {
           return false;
        }else{
           return true;
        }
      }
    },
    getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return decodeURI(r[2])
        }
        return null
    }
  }
}
</script>
