<template>
    <div class="yh-cont" v-show="showMain">
      <div class="keyword-search">
        <div>
          <div style="padding:.2rem .1rem 0 .1rem"><input type="text" placeholder="请输入楼宇（建筑物）名称" v-model="search"></div>
          <div class="searchBox">
            <input type="text" class="select" v-model="checkedNames" style="display:none">
            <div class="down" @click="listDataShow"></div>
            <div v-if="listShow" class="list-show" :style="{height: autoHeight}">
              <ul class="list" v-for="(item,index) in filterData" :key="index" @click="showDifferenceList(index)">
                <li>
                  <label :for="item">{{item.building_name}}</label>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="header-massage">
        <div class="header-massage-first">
          <span>渠道</span>
          <select v-model="buildType" @change="showDifferenceList(-1)">
            <option value="">请选择</option>
            <option :label="item.type_name" :value="item.id" v-for="(item,index) in buildTypeList" :key="index">{{item.type_name}}</option>
          </select>
        </div>
        <div class="header-massage-second">
          <span>楼宇状态 &nbsp;</span>
          <select v-model="buildStatus" @change="showDifferenceList(-1)">
            <option value="">请选择</option>
            <option :label="item.name" :value="item.id" v-for="(item,index) in buildStatusList" :key="index">{{item.name}}</option>
          </select>
        </div>
        <div class="header-massage-third">
          <span>范围</span>
          <select v-model="buildRange" @change="showDifferenceList(-1)">
            <!-- <option value="">请选择</option> -->
            <option :label="item.name" :value="item.id" v-for="(item,index) in buildRangeList" :key="index">{{item.name}}</option>
          </select>
        </div>
      </div>
      <!-- 上拉刷新 -->
      <div class="more-data">
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
            <div>
              <!-- 循环列表 -->
              <div class="search-result" v-for="(item,index) in recordList" :key="index">
                      <ul>
                        <li>楼宇（建筑物）名称：{{item.building_name}}</li>
                        <li>渠道： {{item.build_type_name}}</li>
                        <li>初评建议：{{item.rate_status}}</li>
                        <li>创建时间：{{item.created_at}}</li>
                      </ul>
                      <span class="record-status">{{item.building_status}}</span>
                      <button class="see-detail" @click="editData(index,1)">查看</button>
                      <button class="edit-data" @click="editData(index,2)">编辑</button>
              </div>
            </div>
            <div class="noInner" v-if="!haveMore">暂无更多内容喽！</div>
        </mt-loadmore>
      </div>
    </div>
</template>

<script>
/* eslint-disable */
// import Vue from 'vue'
import axios from 'axios'
// import InfiniteLoading from 'vue-infinite-loading';
import { Indicator } from 'mint-ui'
import { MessageBox } from 'mint-ui'
import { Loadmore } from 'mint-ui'

export default {
  data () {
    return {
      showMain: true,
      keyWord: '',
      buildType: '',
      buildTypeList: [],
      buildStatus: '',
      buildStatusList: [],
      buildRange: '',
      buildRangeList: [],
      recordList: [],
      // editUrl: '/building-record/view',
      editUrl: '/building-record/update',
      searchUrl:'/building-record/find-record-list',
      buildingId: '',
      listShow: false,
      search: '',
      checkedNames: [],
      buildNameList: [],
      autoHeight: "4.3rem",
      sendBranch: '',
      creatorName: '',
      pageval:20,//每次请求的个数
      pagesize:0,//当前页数
      creater_id:"",
      allLoaded:false,//是否加载完毕
      haveMore: true, //是否有更多数据
    }
  },
  computed: {
    filterData: function() {
      var resultList;
      if(this.search!=="") {
        this.listShow = true;
        resultList = this.buildNameList.filter(product=> {
          return Object.keys(product).some(key=> {
            return String(product.building_name).indexOf(this.search) > -1
          })
        });
      } else {
        this.listShow = false;
        resultList = [];
      }
      console.log("resultList..",resultList);
      return resultList;
    },
  },
  watch: { // 监视双向绑定的数据数组
    search:{
      handler(){
        this.listShow = true;
      },
      deep: true // 深度监视
     }
  },
  created() {
    this.init();
  },
  destroyed() {
  },
  methods: {
    init() {
      this.showDifferenceList(-2);
    },
    showDifferenceList(index,event) {
      if(index == -1 || index == -2){
        this.buildingId = "";
      }else {
        this.buildingId = this.filterData[index].id;
      }
      console.log("buildRange",this.buildRange);
      this.listShow = false;
      let params = {
        BuildingRecordSearch: {
          org_id: this.buildRange,
          building_id: this.buildingId,
          building_status: this.buildStatus,
          build_type_id: this.buildType
        },
        page: 0
      };
      axios.post(this.searchUrl,params).then((res)=>{
        console.log("searchUrl res..",res);
        let initData = res.data;
        if(initData.error_code ==0){
          // console.log(initData);
          this.buildNameList = initData.data.buildNameList;
          this.recordList = initData.data.recordList;
          this.pagesize=0;
          if(index==-2) {
            this.buildTypeList = initData.data.buildTypeList;
            this.buildStatusList = initData.data.buildStatus;
            this.buildRangeList = initData.data.range;
          }
        } else {
          MessageBox.alert('',{
            title:'',
            message: initData.msg,
            confirmButtonText:'知道了'
          });
        }
      })
      .catch((error)=>{
        console.log("error..",error);
      });
    },
    loaddata(){
      let params = {
        BuildingRecordSearch: {
          org_id: this.buildRange,
          building_id: this.buildingId,
          building_status: this.buildStatus,
          build_type_id: this.buildType,
        },
        page: this.pagesize
      };
      axios.post(this.searchUrl,params)
      .then((response)=> {
        let datas = response.data;
        console.log("response....",datas);
        if(datas.error_code==0){
             this.buildNameList = datas.data.buildNameList;
             if(this.pagesize == 0){
                this.recordList =datas.data.recordList;
              }else{
                this.recordList = this.recordList.concat(datas.data.recordList);
                if(datas.data.recordList.length == this.pageval){
                  this.haveMore = true;
                }else{
                  this.haveMore = false;
                }
            }
          }else{
            this.alertMsg(datas.msg,'error');
          }
      })
      .catch((error)=> {
        this.alertMsg(error);
      });
    },
    editData(index,id) {
      axios.get(this.editUrl+'?id='+this.recordList[index].record_id).then((res)=>{
        console.log("edit res",res);
        let initData = res;
        if(initData.status!=200){
          MessageBox.alert('',{
            title:'',
            message: initData.statusText,
            confirmButtonText:'知道了'
          });
        }else{
          this.$router.push({
            name: 'NewAndEditBuilding',
            query: {
              id: id,
              // data: initData,
              recordId: this.recordList[index].record_id
            }
          })
        }
      })
      .catch((error)=>{
        console.log("error..",error);
      });
    },
    listDataShow(){
      if (this.search==="") {
        return false;
      }
      this.listShow = !this.listShow;
      if (this.listShow == true){
        this.checkedNames = [];
      }
    },
    heightComputed() {
      if(this.filterData.length > 6){
        console.log('this.autoHeight',this.autoHeight);
        this.autoHeight = "4.3rem";
      }else{
        this.autoHeight = "100%";
      }
    },
    alertMsg(msg,type)
    {
      let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:'知道了'
      });
    },
    loadBottom: function(){//上拉加载更多
       this.more();
       this.$refs.loadmore.onBottomLoaded();
      },
    loadTop: function(){//下拉刷新
      this.pagesize ++;
      this.loaddata();
      this.$refs.loadmore.onTopLoaded();
    },
    more() {
      // 分页查询
      this.pagesize ++;
      console.log("size",this.pagesize);
      this.loaddata();
      this.isHaveMore();
    },
    isHaveMore: function () {
      // 是否还有下一页，如果没有就禁止上拉刷新
      this.allLoaded = true // true是禁止上拉加载
      if (this.haveMore) {
        this.allLoaded = false
      }
    }
  },
  components:{
  }
}
</script>
<style scoped>
  .yh-cont {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-flex-direction: column;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  .keyword-search input{
    width: 99%;
    height: 0.7rem;
  }
  .header-massage{
    width: 100%;
    height: 1rem;
    background-color: #bbb;
    position: relative;
  }
  .header-massage-first{
    position: absolute;
    top: 0.1rem;
    left: 0.1rem;
  }
  .header-massage-first span{
    position: relative;
    /*top: 0.15rem;*/
    left: 0.6rem;
  }
  .header-massage-first select{
    position: relative;
    top: 0.42rem;
    left: -0.5rem;
  }
  .header-massage-second{
    position: absolute;
    top: 0.09rem;
    left: 2.2rem;
  }
  .header-massage-second span{
    position: absolute;
    width: 100%;
    left: 1.1rem;
  }
  .header-massage-second select{
    position: relative;
    top: 0.45rem;
    left: 1rem;
  }
  .header-massage-third{
    position: absolute;
    top: 0.1rem;
    left: 5.4rem;
  }
  .header-massage-third span{
    position: absolute;
    /*width: 100%;*/
    left: 0.9rem;
  }
  .header-massage-third select{
    position: relative;
    top: 0.42rem;
    /*right: 0.05rem;*/
  }
  ul{
    list-style:none;
    margin-left: -0.7rem;
  }
  ul li{
    height: 0.8rem;
  }
  .search-result{
    position: relative;
    height: 4rem;
    width: 7.2rem;
    /*height: 100%;*/
    margin-bottom: 0.5rem;
    margin-left: 0.2rem;
    border-bottom: 1px solid #eee;
    /*padding-left: 0.2rem;
    padding-right: 0.2rem;*/
  }
  .record-status{
    position: absolute;
    top: 0.8rem;
    right: 0.3rem;
  }
  .see-detail{
    position: absolute;
    top: 3rem;
    right: 2.4rem;
    width: 1.5rem;
    height: 0.7rem;
    background-color: #fff;
    border-radius: 0.15rem;
    outline: none;
    border: 0;
  }
  .edit-data{
    position: absolute;
    top: 3rem;
    right: 0.3rem;
    width: 1.5rem;
    height: 0.7rem;
    background-color: #1e7bb3;
    border-radius: 0.15rem;
    outline: none;
    border: 0;
  }
  .searchBox{
    width: 100%;
    height: 0.52rem;
    position: relative;
  }
  .select{
    width: 98%;
    height: 100%;
  }
  .down{
    position: absolute;
    width: 0;
    border:0.18rem solid transparent;
    border-top: 0.22rem solid black;
    top: 0.2rem;
    right: 0.14rem;
    cursor: pointer;
    /*z-index: 10;*/
  }
  .list{
    width: 100%;
    /*border-bottom: 1px solid lightgray;*/
  }
  .list>li{
    height: 0.4rem;
    /*background: white;*/
    border-top: none;
  }
  .list-show{
    background-color: #fff;
    position: absolute;
    top: 0.55rem;
    z-index: 100;
    width: 100%;
    /*height: 4.3rem;*/
    overflow: auto;
  }
  .noInner{
    text-align: center;
    font-size: .26rem;
    color: #333;
    margin:.5rem 0;
  }
  .more-data{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    margin-top:.3rem;;
  }
</style>



